/* Global Reset */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
}

/* Customer Home Page Styling */
.customer-home {
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  text-align: center;
}

.customer-content {
  max-width: 700px;
  padding: 20px;
  background: rgba(255, 255, 255, 0.2);
  backdrop-filter: blur(10px);
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
}

.customer-content h1 {
  font-size: 2.5rem;
  margin-bottom: 20px;
}

.customer-image {
  width: 100%;
  max-width: 450px;
  height: auto;
  border-radius: 10px;
  margin-bottom: 30px;
  box-shadow: 0 4px 8px rgba(255, 255, 255, 0.3);
}

/* Customer Button (Buy Now link) */
.customer-btn {
  background-color: rgba(255, 255, 255, 0.8);
  color: #2575fc;  /* Blue color */
  padding: 12px 25px;
  border-radius: 8px;
  text-decoration: none;
  font-size: 1.3rem;
  font-weight: bold;
  transition: background-color 0.3s ease, transform 0.2s ease;
}

.customer-btn:hover {
  background-color: rgba(255, 255, 255, 1);
  transform: scale(1.05);
}

/* Responsive Design */
@media screen and (max-width: 768px) {
  .customer-content h1 {
    font-size: 2rem;
  }

  .customer-btn {
    font-size: 1.1rem;
    padding: 10px 20px;
  }
}
