/* Global Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Arial', sans-serif;
}

/* Ensure html and body take full height */
html, body {
  height: 100%;
}

body {
  background: linear-gradient(to right, #ff0000, #0000ff);
  color: white;
  padding: 20px;
  min-height: 100vh; /* Ensure body covers the full viewport height */
  display: flex;
  flex-direction: column;
}

/* Header */
h2 {
  color: white;
  font-size: 1.8rem;
  margin-bottom: 20px;
  text-align: center;
}

/* Back to Home Page Link */
a {
  text-decoration: none;
  color: white;
  font-size: 1.2rem;
  margin-bottom: 20px;
  display: inline-block;
}

a:hover {
  color: #ddd;
}

/* Buy Now Link */
a.buy-now {
  color: #2596be; /* Blue color for Buy Now link */
  font-size: 1.2rem;
}

a.buy-now:hover {
  color: #1e7f9d; /* Darker blue on hover */
}

/* Table Styling */
table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 20px;
  background-color: white;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  color: black;
  overflow: auto;
}

th, td {
  padding: 10px;
  text-align: left;
  border: 1px solid black;
  overflow: auto;
}

th {
  background-color: white;
  color: black;
}

tr:nth-child(even) {
  background-color: #f9f9f9;
}

tr:hover {
  background-color: #f1f1f1;
}

/* Button Styles */
button {
  background-color: white;
  color: black;
  padding: 6px 12px;
  border: 1px solid black;
  border-radius: 5px;
  cursor: pointer;
  font-size: 0.9rem;
  margin: 10px 0;
  width: auto;
  max-width: 160px;
  transition: background-color 0.3s ease;
  text-align: center;
}

button:hover {
  background-color: #ddd;
}

/* Form Inputs */
input[type="text"] {
  width: 100%;
  padding: 8px;
  margin: 8px 0;
  border: 1px solid black;
  border-radius: 4px;
  font-size: 0.9rem;
  background-color: white;
  color: black;
}

input[type="text"]:focus {
  outline-color: black;
}

/* Horizontal Layout for Form */
.form-group {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.form-group input {
  width: 48%;
}

.form-group button {
  width: 100%;
  max-width: 150px;
  margin: 10px 0;
}

/* Timestamp Styling */
div {
  font-size: 1rem;
  margin-bottom: 20px;
  font-weight: bold;
}

/* Table Title */
h3 {
  font-size: 1.5rem;
  margin-bottom: 10px;
  color: white;
  border-bottom: 2px solid white;
  padding-bottom: 5px;
  margin-top: 30px;
}

/* Responsive Design */
@media screen and (max-width: 768px) {
  h2 {
    font-size: 1.4rem;
  }

  a, a.buy-now {
    font-size: 1rem;
  }
  table {
    width: 95%; /* Slightly reduce the table width */
  }

  th, td {
    padding: 6px; /* Reduce padding inside table cells */
    font-size: 0.75rem; /* Reduce font size slightly */
    overflow: auto;
  }


  input[type="text"] {
    padding: 5px;
    font-size: 0.8rem;
  }

  button {
    padding: 6px 12px;
    font-size: 0.85rem;
  }

  .form-group input {
    width: 100%;
    margin-bottom: 10px;
  }

  .form-group button {
    width: 100%;
  }

  h3 {
    font-size: 1.3rem;
  }
}

@media screen and (max-width: 480px) {
  h2 {
    font-size: 1.3rem;
  }

  a, a.buy-now {
    font-size: 0.9rem;
  }

  table {
    width: 90%; /* Further reduce table width for smaller screens */
  }

  th, td {
    padding: 4px; /* Further reduce padding */
    font-size: 0.7rem; /* Adjust font size */
    overflow: auto;
  }


  input[type="text"] {
    padding: 5px;
    font-size: 0.8rem;
  }

  button {
    padding: 5px 10px;
    font-size: 0.8rem;
  }

  h3 {
    font-size: 1.1rem;
  }

  .form-group {
    flex-direction: column;
  }

  .form-group input {
    width: 100%;
    margin-bottom: 10px;
  }

  .form-group button {
    width: 100%;
  }
}

/* Back to Home Link Styling */
.back-home-link {
  text-decoration: none;
  color: rgb(0, 0, 0);
  font-size: 1.2rem;
  margin-bottom: 20px;
  display: inline-block;
}

.back-home-link:hover {
  color: #ddd;
}