/* Global Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Arial', sans-serif;
}

body {
  background: linear-gradient(to right, #ff0000, #0000ff);
  color: #ffffff;
  font-size: 16px;
}

/* Home Page Container */
.home-page {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  text-align: center;
  padding: 20px;
  color: white;
}

/* Home Content */
.home-content {
  background-color: rgba(243, 234, 234, 0.5); /* Semi-transparent background */
  padding: 40px;
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.4);
  width: 100%;
  max-width: 600px;
}

.home-content h1 {
  font-size: 2.5rem;
  color: #fff;
  margin-bottom: 20px;
}

.home-content p {
  font-size: 1.2rem;
  margin-bottom: 20px;
  color: #ddd;
}

.home-image {
  width: 100%;
  max-width: 400px;
  margin: 0 auto 30px;
  border-radius: 8px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
}

.btn {
  background-color: #ff0000;
  color: white;
  padding: 14px 28px;
  border-radius: 5px;
  text-decoration: none;
  font-size: 1.2rem;
  display: inline-block;
  margin-top: 20px;
  border: 2px solid rgb(0, 0, 0);
}

.btn:hover {
  background-color: #cc0000;
  transition: background-color 0.3s ease;
}

@media screen and (max-width: 768px) {
  .home-content h1 {
    font-size: 2rem;
  }

  .home-content p {
    font-size: 1rem;
  }

  .home-image {
    max-width: 100%;
  }

  .btn {
    font-size: 1rem;
    padding: 12px 24px;
  }
}