.navbar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: red;
    padding: 10px 20px;
    color: white;
    position: relative;
  }
  
  .logo img {
    height: 35px;
    cursor: pointer;
  }
  
  .hamburger {
    display: none;
    font-size: 24px;
    cursor: pointer;
    color: white;
  }
  
  .nav-links {
    display: flex;
    gap: 30px;
  }
  
  .nav-section {
    display: flex;
    gap: 15px;
  }
  
  .nav-section a {
    text-decoration: none;
    color: white;
    font-weight: bold;
    padding: 5px 10px;
    transition: 0.3s;
  }
  
  .nav-section a:hover {
    background-color: rgba(255, 255, 255, 0.2);
    border-radius: 5px;
  }
  
  /* Responsive styles */
  @media (max-width: 768px) {
    .hamburger {
      display: block;
    }
  
    .nav-links {
      display: none;
      flex-direction: column;
      position: absolute;
      top: 60px;
      left: 0;
      width: 100%;
      background-color: red;
      text-align: center;
    }
  
    .nav-links.active {
      display: flex;
    }
  
    .nav-section {
      flex-direction: column;
      padding: 10px 0;
    }
  }
  